input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 30px white inset !important;
}
.dx-ui-input-addon-select {
  position: absolute;
  background: transparent;
  right: 0px;
  bottom: 0px;
  width: 50px;
  line-height: 32px;
  border: 1px solid #d9d9d9;
  border-left: none;
  height: 100%;
  background-color: #EDF4FF;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  font-size: 14px;
  z-index: 2;
  color: #000;
}
.dx-ui-input-addon-select + input {
  padding-right: 50px;
}
.dx-ui-input-select {
  position: relative;
}
.dx-ui-input-select .ant-input-suffix {
  transform: translate(-48px, -50%);
  background: #fff;
}
.dx-ui-input-select .ant-input-affix-wrapper {
  display: flex;
}
.dx-ui-input-select .ant-input-affix-wrapper .ant-input:not(:last-child) {
  padding-right: 73px;
}
.dx-ui-input-select input {
  resize: none;
  margin-top: 0px !important;
  width: auto;
}
.dx-ui-input-select .ant-input {
  flex: 1 1;
  line-height: auto !important;
  border-radius: 0px !important;
}
.ul-select {
  position: absolute;
  width: 100%;
  z-index: 1500;
  padding-bottom: 0px;
  border: 1px solid #cccccc;
  border-top: none;
  background-color: #cccccc;
  max-height: 300px ;
  height: auto;
}
.ul-select li {
  height: 50px;
  padding-right: 18px;
  padding-left: 11px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #FFFFFF;
  border-bottom: 1px solid #CCCCCC;
}
.ul-select li:hover {
  background-color: #F9FBFF;
  cursor: pointer;
}
.ul-select li:last-child {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #EDF4FF;
  cursor: pointer;
  border-bottom: 0px !important;
}
.ul-select li:last-child span:last-child {
  width: 84px;
  height: 14px;
  font-size: 14px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: #51a5ff;
  line-height: 14px;
}
