.cashout .cashout-body {
  width: 100%;
  border: 1px solid rgba(0, 0, 0, 0.06);
  box-shadow: 0px 12px 24px 0px #f4f4f4;
  padding: 27px 40px 40px;
  margin-bottom: 32px;
}
.cashout .cashout-body .cashout-none {
  width: 295px;
  margin: 0 auto;
  padding-bottom: 80px;
}
.cashout .cashout-body .cashout-none img {
  margin: 0 auto;
  display: block;
}
.cashout .cashout-body .cashout-none .cashout-text p {
  text-align: center;
  padding-top: 30px;
}
.cashout .cashout-body .cashout-body-header {
  width: 100%;
  border-bottom: 1px solid #f0f0f0;
  padding-bottom: 28px;
  font-size: 24px;
  font-weight: 500;
}
.cashout .cashout-body .cashout-body-content {
  width: 100%;
  padding: 40px 60px;
}
.cashout .cashout-body .cashout-body-content .form-lable {
  font-size: 16px;
  font-weight: 500;
  display: block;
  margin-bottom: 16px;
  color: #333333;
}
.cashout .cashout-body .cashout-body-content .form-lable-right {
  color: #51A5FF;
  cursor: pointer;
}
.cashout .cashout-body .cashout-body-content .form-box {
  width: 320px;
  height: 50px;
  padding: 0 18px;
  background: #F7FAFF;
  border-radius: 4px;
  margin-bottom: 24px;
  line-height: 50px;
}
.cashout .cashout-body .cashout-body-content .titles {
  margin-top: 32px;
  font-size: 14px;
  color: #999999;
  font-weight: 400;
}
.cashout .cashout-body .cashout-body-content .titles .con {
  margin-top: 16px;
}
.cashout .cashout-body .cashout-body-content .titles p {
  margin: 0;
}
.cashout .personal-title {
  margin-top: 10px;
  font-size: 16px;
  margin-bottom: 16px;
  color: #000;
}
.cashout .table-header {
  font-size: 16px;
  font-weight: 400;
}
.cashout .table-header .right {
  color: #51A5FF;
  cursor: pointer;
}
.cashout .table-table {
  width: 100%;
  padding: 24px;
  box-shadow: 0px 12px 24px 0px #f4f4f4;
  border: 1px solid rgba(0, 0, 0, 0.06);
  margin-top: 16px;
}
.cashout .table-table .qu-btn {
  color: #0091ff;
  cursor: pointer;
}
.cashout .table-table .open-box {
  padding: 0px 27px;
}
.cashout .table-table .open-box .open-box-con {
  padding: 19px 0 24px;
  border-bottom: 1px solid #F2F2F2;
}
.cashout .table-table .open-box .open-box-con .hash {
  color: #0091ff;
  cursor: pointer;
}
.cashout .table-table .open-box .open-box-con .text {
  padding-left: 20px;
}
.cashout .table-table .open-box .open-box-con .copy {
  width: 61px;
  height: 24px;
  line-height: 24px;
  background: #4f97ff;
  border-radius: 12px;
  text-align: center;
  color: #fff;
  font-size: 12px;
  cursor: pointer;
}
.cashout .table-table .ant-table-expanded-row {
  background: #fff;
}
.cashout .ant-input {
  border-radius: 0;
}
@media only screen and (max-width: 768px) {
  .cashout .cashout-body {
    padding: 15px 15px 20px;
  }
  .cashout .cashout-body .cashout-none {
    width: 100%;
    padding-bottom: 40px;
  }
  .cashout .cashout-body .cashout-none .cashout-text {
    padding-left: 10px;
  }
  .cashout .cashout-body .cashout-body-header {
    padding-bottom: 14px;
  }
  .cashout .cashout-body .cashout-body-content {
    padding: 20px 10px;
  }
  .cashout .cashout-body .cashout-body-content .form-lable {
    margin-bottom: 5px;
  }
  .cashout .cashout-body .cashout-body-content .form-box {
    margin-bottom: 12px;
    width: 100%;
  }
  .cashout .table-table {
    padding: 10px;
  }
  .cashout .table-header {
    font-size: 14px;
  }
  .cashout .ant-form-item {
    margin-bottom: 0px;
  }
}
